.main {
  background-color: #f5f5f5;
  position: absolute;
  padding: 1em;
  top: 60px;
  right: 0;
  bottom: 0;
  left: 0;
  margin-left: 0px;
  overflow-y: auto;
  transition: margin-left 0.5s ease-in-out;
}

.mainMin {
  margin-left: 70px;
}

.mainOpen {
  margin-left: 240px;
}
