.patientProfile h1 {
  margin-top: 0;
  margin-left: 1em;
  color: dimgray; }
  .patientProfile h1 small,
  .patientProfile h1 span {
    color: gray;
    font-size: 18px; }
  .patientProfile h1 small {
    padding-left: 1em; }

.patientProfile .gridAreasApp {
  grid-template-areas: "PD APP" "CH  APP"; }

.patientProfile .gridAreas {
  grid-template-areas: "PD CH"; }

.patientProfile .superior {
  padding: 1em;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 2em; }
  .patientProfile .superior .appointments {
    grid-area: APP; }
  .patientProfile .superior .personalData {
    grid-area: PD; }
  .patientProfile .superior .clinicalHistory {
    grid-area: CH; }
  .patientProfile .superior .consultation .sectionHeader,
  .patientProfile .superior .personalData .sectionHeader,
  .patientProfile .superior .clinicalHistory .sectionHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 6px 0 1em;
    position: relative;
    background: -webkit-linear-gradient(45deg, #46d0f7, #7d2ae8);
    background: linear-gradient(45deg, #46d0f7, #7d2ae8);
    color: white;
    border-top-right-radius: 12px;
    border-top-left-radius: 12px; }
    .patientProfile .superior .consultation .sectionHeader button,
    .patientProfile .superior .personalData .sectionHeader button,
    .patientProfile .superior .clinicalHistory .sectionHeader button {
      margin: auto 0;
      padding: 6px; }
  .patientProfile .superior .consultation .sectionContent,
  .patientProfile .superior .personalData .sectionContent,
  .patientProfile .superior .clinicalHistory .sectionContent {
    padding: 6px;
    margin: 0;
    margin-top: -10px;
    border: solid 1px transparent;
    border-radius: 12px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(white), to(white)), radial-gradient(circle at top left, #46d0f7, #7d2ae8);
    background-image: -webkit-linear-gradient(white, white), -webkit-radial-gradient(top left, circle, #46d0f7, #7d2ae8);
    background-image: linear-gradient(white, white), radial-gradient(circle at top left, #46d0f7, #7d2ae8);
    background-origin: border-box;
    background-clip: content-box, border-box; }
    .patientProfile .superior .consultation .sectionContent .content,
    .patientProfile .superior .personalData .sectionContent .content,
    .patientProfile .superior .clinicalHistory .sectionContent .content {
      padding: 1em;
      display: grid; }
      .patientProfile .superior .consultation .sectionContent .content > div,
      .patientProfile .superior .personalData .sectionContent .content > div,
      .patientProfile .superior .clinicalHistory .sectionContent .content > div {
        margin: 5px 1em; }
      .patientProfile .superior .consultation .sectionContent .content .telyemail,
      .patientProfile .superior .consultation .sectionContent .content .genydob,
      .patientProfile .superior .personalData .sectionContent .content .telyemail,
      .patientProfile .superior .personalData .sectionContent .content .genydob,
      .patientProfile .superior .clinicalHistory .sectionContent .content .telyemail,
      .patientProfile .superior .clinicalHistory .sectionContent .content .genydob {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 1em; }
      .patientProfile .superior .consultation .sectionContent .content .dob input::-webkit-inner-spin-button,
      .patientProfile .superior .personalData .sectionContent .content .dob input::-webkit-inner-spin-button,
      .patientProfile .superior .clinicalHistory .sectionContent .content .dob input::-webkit-inner-spin-button {
        display: none; }
      .patientProfile .superior .consultation .sectionContent .content .dob input::-webkit-calendar-picker-indicator,
      .patientProfile .superior .personalData .sectionContent .content .dob input::-webkit-calendar-picker-indicator,
      .patientProfile .superior .clinicalHistory .sectionContent .content .dob input::-webkit-calendar-picker-indicator {
        opacity: 0; }
      .patientProfile .superior .consultation .sectionContent .content .dob input,
      .patientProfile .superior .personalData .sectionContent .content .dob input,
      .patientProfile .superior .clinicalHistory .sectionContent .content .dob input {
        background: #fff url(https://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/calendar_2.png) 93% 50% no-repeat; }
  .patientProfile .superior .appointments .tabsbar .MuiTabs-flexContainer-160 {
    border-bottom: 1px solid lightgray; }
  .patientProfile .superior .appointments .consultation {
    margin: 2em auto; }
    .patientProfile .superior .appointments .consultation .sectionContent .content {
      display: grid; }
      .patientProfile .superior .appointments .consultation .sectionContent .content .vitalsigns {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        grid-gap: 1em; }
      .patientProfile .superior .appointments .consultation .sectionContent .content .row1,
      .patientProfile .superior .appointments .consultation .sectionContent .content .row2 {
        display: grid;
        grid-gap: 1em;
        grid-template-columns: 1fr 1fr; }
  .patientProfile .superior .clinicalHistory .sectionContent .content {
    display: grid; }
    .patientProfile .superior .clinicalHistory .sectionContent .content .row0 {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 1em; }
    .patientProfile .superior .clinicalHistory .sectionContent .content .row1,
    .patientProfile .superior .clinicalHistory .sectionContent .content .row2 {
      display: grid;
      grid-gap: 1em;
      grid-template-columns: 1fr 1fr; }
  .patientProfile .superior .appointments {
    text-align: center; }

